import * as Utils from './utils'

console.log('hello')

function update () {
  const wdth = Utils.map(window.innerWidth, 500, 2000, 100, 1000, true)
  const wght = Utils.map(window.innerWidth, 500, 2000, 700, 100, true)

  document.body.style['font-variation-settings'] = `'wdth' ${wdth}, 'wght' ${wght}`
  requestAnimationFrame(update)
}

requestAnimationFrame(update)